header{
    height: 10vh;
    line-height: 10vh;
}

header .container {
    padding-top: 15px;
}

header ul li {
    margin-left: 30px;
}

header ul li a {
      transition: 0.5s;
      font-size: 13px;
      font-weight: 500;
      letter-spacing: 1px;
}

header ul li a:hover{
    color:red;
}

.home-btn{
    padding: 18px;
    border-radius: 6px;
    transition: 0.3s all ease;
    height: fit-content;
    color: red;
}

.home-btn:hover{
   color: white;
    background-color: rgb(186, 61, 61);
}
.open{
    display: none;
}

