.hero{
    position: relative;
}
.hero h3{
    font-weight: 100;
    letter-spacing: 2px;
}
 .hero h1{
     font-size: 60px;
     font-weight: bold;
 }

 .hero h1 span{
     color: brown;
 }

 .hero h2{
     font-size: 50px;
     font-weight: bold;
     color: rgb(75, 19, 19);
 }
 .hero_btn{
     margin-top: 10%;
   

 }

 .hero_btn h4{
    font-weight: 400;
    letter-spacing: 2px;
    color: #43474b;
    margin: 30px 0 30px 0;
 }

 .hero_btn button img{
     width: 20px;
     height: 20px;
   
 }
 .hero_btn button {
  margin-left: 10px;
  margin-top: 10px;
}

.hero_btn #right_col{
    margin-left: 100px;
}
.right_img{
    position: relative;
    background: linear-gradient(145deg,#e9f0f0, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb , 4px -4px 8px #ffffff;
    border-radius: 50%;
    width: 400px;
    margin-top: 20%;
    margin-left: 25%;
}

.right_img img{
 
   width: 290px;
  margin-right: 20%;
  margin-bottom: 10%;
  border-radius: 20%;
  
}