.container{
 
    text-align: center;
}
.projectscard{
      display: flex;
      margin-left: 10%;
}
.projectscard h1{
    
    margin-top: 10%;
}
.card{
    background: linear-gradient(145deg,#e9f0f0, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb , 4px -4px 8px #ffffff;
    width: 300px;
    height: 300px;
    margin-left: 30px;
    justify-content: center;
    margin-bottom: 20%;
    margin-top: 5%;
    transition: 1s;
   
}

.card:hover{
  
    transform: translateY(-10px);  
}


.card p {
    font-size: 10px;
}